.fd-info-wrap{
  margin-top: 25px;
}
.fd-info-wrap .tab-bar{
  display: flex;
  background-color: #f7f7f7;
}
.fd-info-wrap .tab-bar .bar-item{
  box-sizing: border-box;
  width: 128px;
  height: 40px;
  font-size: 14px;
  line-height: 38px;
  text-align: center;
  border-top: 2px solid #e8e8e8;
  border-right: 1px solid #e8e8e8;
  background-color: #f7f7f7;
  color: #666666;
}
.fd-info-wrap .tab-bar .bar-item:hover,.fd-info-wrap .tab-bar .is-active{
  border-top-color: #ffd928;
  border-right-width: 0;
  background-color: #fff;
}
.fd-info-wrap .contain-bottom{
  padding:20px 0;
  background-color: #fff;
}
.fd-info-wrap .contain-bottom .detail-wrap{
  padding-left: 90px;
}
.fd-info-wrap .contain-bottom .parameter{
  line-height: 12px;
  color: #666666;
  margin-bottom: 6px;
}
.fd-info-wrap .contain-bottom .parameter-detail{
  display: flex;
  
}
.contain-bottom .parameter-detail .item{
  flex:1;
}
.parameter-detail .item .item-info{
  height: 12px;
  line-height: 12px;
  color: #666666;
  margin-top: 16px;
}
//试用车型
.contain-bottom .car-wrap{
  padding: 0 30px;
}
.contain-bottom .car-wrap .car-list{
  border-bottom: 1px dashed #e5e5e5;
  padding: 10px 0;
  font-size: 12px;
}
.contain-bottom .car-wrap .car-list:last-child{
  border-bottom-width:0; 
}
.contain-bottom .car-wrap .car-list .car-brand{
  margin-bottom: 20px;
  line-height: 12px;
  color: #666;
}
.contain-bottom .car-wrap .car-list .son-brand span{
  display: inline-block;
  line-height: 12px;
  color: #c27979;
  margin-right: 10px;
}
//技术资料
.contain-bottom .vadio-list{
  padding: 0 30px;
}
.contain-bottom .vadio-list .vadio-item{
  height: 36px;
  line-height: 36px;
  border-bottom: 1px dashed #e5e5e5;
}