.floor-detail{
  box-sizing: border-box;
  padding: 30px 0px 17px;
  background-color: #fff;
  // border: 1px solid #e8e8e8;
}
.floor-detail .fd-goods-info{
  display: flex;
  padding: 24px;
}
.fd-info-left{
  width: 400px;
  margin-right: 22px;
}
.fd-info-right{
  flex: 1;
}
.fd-info-right .goods-name{
  height: 38px;
  line-height: 20px;
  padding-bottom: 18px;
  border-bottom: 1px solid #e5e5e5;
  font-size: 20px;
  color: #4d4d4d;
}
.fd-info-right .right-main .price-info{
  height: 56px;
  line-height: 56px;
  padding-left: 8px;
  border-bottom: 1px dotted #e5e5e5;
}
.price-info .price{
  float: left;
}
.price-info .price .price-color{
  font-size: 18px;
  color: #ff3333;
}
.price-info .price .price-color i{
  font-size: 30px;
}
.price-info .goods-number{
  float: right;
  margin-right: 28px;
  color: #999999;
}
.price-info .goods-number .number-color{
  color:#666666;
}
.price-info .collection{
  float: right;
}
.right-main .good-active{
  padding: 14px 0px 4px 8px;
  border-bottom:1px dashed #e5e5e5;
}
.right-main .good-active .active-logo{
  display: inline-block;
  width: 36px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  background-color: #fdd000;
  color: #ffffff;
}
.good-active .active-logo-wrap .active-logo{
  margin-right: 14px;
}
.good-active .active-logo-wrap .active-more{
  float: right;
  color:#1882f8;
}
.right-main .good-active .active-tip{
  line-height: 12px;
  margin-bottom: 14px;
  color: #999;
}
.good-active .active-single{
  display: flex;
  margin-bottom: 10px;
}
.good-active .active-desc{
  flex: 1;
  margin-left: 10px;
}
// 货物仓库数量等信息
.floor-detail .fd-goods-info .goods-info{
  font-size: 12px;
  padding: 20px 8px; 
}
.goods-info .goods-info-list{
  display: flex;
  height: 30px;
  line-height: 30px;
  margin-bottom: 10px;
  color:#999999;
}
.goods-info .goods-info-list .label-name{
  width: 62px; 
}
.goods-info .goods-info-list .list-info{
  position: relative;
  min-width: 170px;
  padding-left:10px; 
  margin-right: 10px;
  border: 1px solid #e5e5e5;
}
.goods-info .goods-info-list .list-info:hover{
  cursor: pointer;
  border: 1px solid #ffd928;
}
.goods-info .goods-info-list .list-info .active-icon{
  line-height: 16px;
  color: #fff;
  border-radius: 8px;
  padding: 0 6px;
  margin-right: 4px;
  background-image: linear-gradient(
		#ff6633, 
		#ff6633), 
	linear-gradient(
		#fc353a, 
		#fc353a);
	background-blend-mode: normal, 
		normal;
}
.goods-info .goods-info-list .list-info .active-icon1{

}
.goods-info .goods-info-list .list-info .xiala{
  position: absolute;
  top:8px;
  right: 8px;
}
.right-main .add-shop-btn{
  margin-left: 70px;
}