.big-img{
  width: 400px;
  height: 400px;
  background-color:red;
}
.big-img img{
  background-size: contain;
}
.img-wrap{
  font-size: 0;
  margin-top: 14px;
}
.img-wrap .smal-img{
  box-sizing: border-box;
  height: 60px;
  width: 60px;
  display: inline-block;
  border: 1px solid #e8e8e8;
  margin-right: 8px;
  background-color: yellow;
}
.img-wrap .smal-img:last-child{
  margin-right: 0;
}